import React, {useState} from 'react';
import {useTranslation} from 'react-i18next';
import {renderToStaticMarkup} from 'react-dom/server';
import {Map as LeafletMap, Marker, Popup, TileLayer} from 'react-leaflet';
import {divIcon} from 'leaflet';
import {styled, useStyletron} from 'baseui';
import MarkerClusterGroup from 'react-leaflet-markercluster';
import {Spinner} from 'baseui/spinner';
import {Paragraph2, Label1} from 'baseui/typography';
import {StyledBody} from 'baseui/card';
import {Block} from 'baseui/block';
import {StyledCard} from './..';
import {Marker as MarkerIcon} from './..';
import {Figure} from '../Figures/Figures';

import {useData} from '../../contexts/DataContext';
import groupBy from 'lodash.groupby';
import useWindowDimensions from '../../hooks/window-dimensions';
import {sum} from '../../helpers/misc';

const MIN_MARKER_SIZE = 90;
const MAX_MARKER_SIZE = 180;

const Centered = styled('div', ({$theme}) => ({
    backgroundColor: $theme.colors.backgroundPrimary,
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    flexDirection: 'column',
    height: 'auto',
    width: '100vw',
    [$theme.mediaQuery.medium]: {
        height: '100vh',
    },
}));

function createMarkerIcon(size, registrationsCount, votesCount) {
    return divIcon({
        iconSize: [size, size],
        html: renderToStaticMarkup(
            <MarkerIcon
                size={size}
                registrationsCount={registrationsCount}
                votesCount={votesCount}
            />
        ),
    });
}

function getMarkerSize(max, count) {
    return (count / max) * (MAX_MARKER_SIZE - MIN_MARKER_SIZE) + MIN_MARKER_SIZE;
}

function getLocationForStation(clickedStation, data) {
    return data
        .filter((item) => item.stations.name === clickedStation)
        .pop().stations.location;
}

export default function Map(props) {
    const {t} = useTranslation();
    const [activeStation, setActiveStation] = useState(null);
    const {width} = useWindowDimensions();
    const [, theme] = useStyletron();

    const {
        stations,
        registrations,
        votes,
        candidates,
        isLoading,
        clickedStation,
    } = useData();

    if (isLoading) {
        return (
            <Centered>
                <Spinner/>
                <Paragraph2>{t('loadingData')}</Paragraph2>
            </Centered>
        );
    }

    const groupedRegistrations = groupBy(registrations, 'stationId');
    const groupedVotes = groupBy(votes, 'stationId');
    const groupedCandidates = groupBy(candidates, 'stationId');

    let data = [];

    if (!data.length) {
        for (const station of stations) {
            data.push({
                station,
                registrations: {
                    total: sum(groupedRegistrations[station.sn]),
                    data: groupedRegistrations[station.sn] || [],
                },
                votes: {
                    total: sum(groupedVotes[station.sn]),
                    data: groupedVotes[station.sn] || [],
                },
                candidates: {
                    total: sum(groupedCandidates[station.sn]),
                    data: groupedCandidates[station.sn] || [],
                },
            });
        }
    }

    const max = Math.max(...data.map(({registrations}) => registrations.total));
    const position = clickedStation
        ? getLocationForStation(clickedStation, data)
        : [9.005401, 38.763611];

    return (

        <LeafletMap
            center={position}
            zoom={clickedStation ? 10 : width < theme.breakpoints.medium ? 4 : 5}
            zoomControl={false}
            maxZoom={15}
            minZoom={width < theme.breakpoints.medium ? 6 : 7}
            maxBounds={[
                [3, 30],  // Southwest coordinates
                [15, 50]] // Northeast coordinates
            }
            {...props}
        ><TileLayer
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            attribution="&copy; <a href=&quot;http://osm.org/copyright&quot;>OpenStreetMap</a> contributors"
        />

            <MarkerClusterGroup
                showCoverageOnHover={false}
                iconCreateFunction={(cluster) => {
                    const count = cluster.getAllChildMarkers().reduce(
                        (total, marker) => ({
                            registrations: marker.options.registrationsCount + total.registrations,
                            votes: marker.options.votesCount + total.votes,
                        }),
                        {
                            registrations: 0,
                            votes: 0,
                        }
                    );

                    return createMarkerIcon(
                        getMarkerSize(max, count.registrations),
                        count.registrations,
                        count.votes
                    );
                }}
            >
                {data &&
                data.map(({station, registrations, votes, candidates}) => (
                    <Marker
                        key={station.id}
                        position={station.location}
                        icon={createMarkerIcon(
                            getMarkerSize(max, registrations.total),
                            registrations.total,
                            votes.total
                        )}
                        onClick={() => {
                            setActiveStation({...station, registrations, votes, candidates});
                        }}
                        registrationsCount={registrations.total}
                        votesCount={votes.total}
                    />
                ))}
            </MarkerClusterGroup>
            {activeStation && (
                <Popup
                    position={activeStation.location}
                    onClose={() => setActiveStation(null)}
                >
                    <StyledCard
                        style={($theme) => ({
                            [$theme.mediaQuery.large]: {
                                width: '380px',
                            },
                        })}
                    >
                        <StyledBody>
                            <Label1>{activeStation.np_election_zone}</Label1>

                            <Block marginTop='10px'>
                                <Figure
                                    data={activeStation.votes.data}
                                    label={t('votes')}
                                    color={theme.colors.positive}
                                    size='compact'
                                />

                                <Figure
                                    data={activeStation.registrations.data}
                                    label={t('registered')}
                                    color={theme.colors.primary}
                                    size='compact'
                                />
                                {/*Candidates tables*/}
                            </Block>
                        </StyledBody>
                    </StyledCard>
                </Popup>
            )}
        </LeafletMap>
    );
}
