const saveLanguageToStorage = (lang) => {
    localStorage.setItem('lang', lang);
};

export const switchLanguage = ({i18n}) => {
    if (i18n.language === 'am') {
        i18n.changeLanguage('en');
        saveLanguageToStorage('en');
    } else {
        i18n.changeLanguage('am');
        saveLanguageToStorage('am');
    }
};
